import {SEND_TYPE,ENVIRONMENT} from '../constant'
import Base64 from "../utils/base64"
import { Config } from '@/types';
//default config
let config:Config={
  pageTime:true,  //
  watchHistoryAndHash:true,
  env:ENVIRONMENT.PRODUCTION,
  console:true,
  projectId:null,
  token:null,
  version:null,
  domain:'',
  sendType:SEND_TYPE.ASYNC,  // (,,)
  delayTime:1000,  //
  autoTrakerPage:true,
  autoTrakerClick:true,//a,button,input
  autoInstall:true,
  delayLink:true,
  delayLinkTime:200,
  useServerTime:true,
  corssSubdomain:true,  //false 
  analyseScript:"../dist/analyse.min.js",
  performance:true
}

//script tracker-key  config
let scriptDom = document.querySelector('script[tracker-key]')
if(scriptDom){
  let newConfig = Base64.decode(scriptDom.getAttribute('tracker-key') || '');
  if(newConfig){
    config={...config,...JSON.parse(newConfig)}
  }
}



export function getConfig(){
  return config
}

export function setConfig(data:Config){
  config={...config,...data}
}
